
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.wrapper {
  max-width: 1100px;
  max-width: calc(1100px + 1em);
  margin: 0 auto;
}
.wrapper h1 {
  margin: 0 0 0 11.156px;
  text-align: left;
}
.wrapper h1 a {
  display: inline-block;
  text-decoration: none;
  line-height: 26px;
}
.wrapper h1 a:after {
  content: '';
  height: 3px;
  width: 0px;
  display: block;
  margin: auto;
  background: transparent;
  transition: width 0.5s ease, background-color 0.5s ease, font-weight 0.5s ease;
}
.wrapper h1 a:hover:after {
  position: relative;
  width: 98.2%;
  left: 0.8%;
  background: blue;
}
nav {
  background-color: #fff;
  margin-bottom: 10px;
}
nav a {
  position: relative;
  display: inline-block;
  color: #2d2d2d;
  padding: 10px 0;
  margin: 0 10px;
  line-height: 20px;
  font-size: 0.8em;
  text-transform: uppercase;
  text-decoration: none;
}
nav a:after {
  content: '';
  height: 1px;
  width: 0px;
  display: block;
  margin: auto;
  background: transparent;
  transition: width 0.5s ease, background-color 0.5s ease, font-weight 0.5s ease;
}
nav a:hover {
}
nav a:hover:after {
  width: 100%;
  background: #2d2d2d;
}
.router-link-active {
}
.router-link-active:after {
  width: 100%;
  background: #2d2d2d;
}



